import React, { Component } from 'react';
import { render } from 'react-dom';
import IframeComponent from './IframeComponent';


class road extends Component {
  constructor() {
    super();
    this.state = {
      name: 'React'
    };
  }

  render() {
    return (
      <div>
        <IframeComponent src="./road1.html" height="100%" width="100%"/>
      </div>
    );
  }
}

export default road;
